    /* Float four columns side by side */
    .column {
      float: left;
      width: 300px;
      padding: 0 10px ;
      margin-left: 10px;
      box-sizing: border-box;
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-flex-grow: 1;
              flex-grow: 1;
      -webkit-justify-content: center;
              justify-content: center;
      height: auto;
      min-height: 88px;
      margin-right: 15px;
      
    }
