        box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
        width: auto;
        height: auto;
    }
    
    
    
   /* Remove extra left and right margins, due to padding */
  .middlepart .row {margin: 0 -50px;}
   
  /* Clear floats after the columns */
  .row:after {
      content: "";
      display: table;
      clear: both;
    }
    
    /* Responsive columns */
    @media  screen and (max-width:1000px) {
      .mlclass{
          margin: auto;
          margin-left: 100px;
